<template>
    <div class='aside'>
               
<el-menu
id='sidebar'
:unique-opened=true
active-text-color="#ffd04b"
background-color = "#0E2D43"
text-color='#fbfbfb'
default-active="1-4-1"
class="el-menu-vertical-demo"
@open="handleOpen"
@close="handleClose"
:collapse="isCollapse"
router>
  <el-menu-item index="/home/index">
    <span slot="title">首页</span>
  </el-menu-item>
  <el-submenu index="2">
    <template slot="title">
      <span slot="title">统计</span>
    </template>
    <el-menu-item-group>
      <el-menu-item index="/statistics/statistics_site">站点统计</el-menu-item>
      <el-menu-item index="/statistics/statistics_amount">资金统计</el-menu-item>
      <el-menu-item index="/statistics/statistics_order">订单统计</el-menu-item>
      <el-menu-item index="/statistics/statistics_lang?lg=cn">中文站点</el-menu-item>
      <el-menu-item index="/statistics/statistics_lang?lg=en">英文站点</el-menu-item>
    </el-menu-item-group>
  </el-submenu>
  <el-submenu index="3">
    <template slot="title">
      <span slot="title">用户管理</span>
    </template>
    <el-menu-item-group>
      <el-menu-item index="/usermanage/user_list">列表</el-menu-item>
    </el-menu-item-group>
  </el-submenu>
  <el-submenu index="4">
    <template slot="title">
      <span slot="title">商品管理</span>
    </template>
    <el-menu-item-group>
      <el-menu-item index="/productmanage/product_list">列表</el-menu-item>
      <el-menu-item index="/productmanage/product_insert">新建商品</el-menu-item>
    </el-menu-item-group>
  </el-submenu>
  <el-submenu index="5">
    <template slot="title">
      <span slot="title">订单管理</span>
    </template>
    <el-menu-item-group>
      <el-menu-item index="/ordermanage/order_list">订单管理</el-menu-item>
    </el-menu-item-group>
  </el-submenu>
  <el-submenu index="6">
    <template slot="title">
      <span slot="title ">托管管理</span>
    </template>
    <el-menu-item-group>
      <el-menu-item index="/ordermanage/hosting">列表</el-menu-item>
    </el-menu-item-group>
  </el-submenu>
  <el-submenu index="7">
    <template slot="title">
      <span slot="title">充值信息</span>
    </template>
    <el-menu-item-group>
      <el-menu-item index="/fundHistory">列表</el-menu-item>
    </el-menu-item-group>
  </el-submenu>
  <el-submenu index="8">
    <template slot="title">
      <span slot="title">提现信息</span>
    </template>
    <el-menu-item-group>
      <el-menu-item index="/withdrawHistory">列表</el-menu-item>
    </el-menu-item-group>
  </el-submenu>
  <el-submenu index="9">
    <template slot="title">
      <span slot="title">优惠码管理</span>
    </template>
    <el-menu-item-group>
      <el-menu-item index="/coupon/send_coupon">生成优惠码</el-menu-item>
      <el-menu-item index="/coupon/coupon_list">优惠码活动列表</el-menu-item>
      <el-menu-item index="/coupon/add_coupon">创建优惠码活动</el-menu-item>
    </el-menu-item-group>
  </el-submenu>
  <el-submenu index="10">
    <template slot="title">
      <span slot="title">售后管理</span>
    </template>
    <el-menu-item-group>
      <el-menu-item index="/ordermanage/after_order_add">新建售后订单</el-menu-item>
      <el-menu-item index="/ordermanage/after_order_list">售后订单列表</el-menu-item>
    </el-menu-item-group>
  </el-submenu>
  <el-submenu index="11">
    <template slot="title">
      <span slot="title">线下订单管理</span>
    </template>
    <el-menu-item-group>
      <el-menu-item index="/offlinemanager/offline_user">线下用户列表</el-menu-item>
    </el-menu-item-group>
  </el-submenu>
  <el-submenu index="12">
    <template slot="title">
      <span slot="title">工具</span>
    </template>
    <el-menu-item-group>
      <el-menu-item index="/banner">官网banner管理</el-menu-item>
      <el-menu-item index="/tool/coupon_send">发送优惠券</el-menu-item>
      <el-menu-item index="/tool/export">数据导出</el-menu-item>
    </el-menu-item-group>
  </el-submenu>
</el-menu>
    </div>
</template>

<script>
import { mapState } from "vuex";
export default {
    name: "side",
    data() {
      return {
        isCollapse: false
      };
    },
    methods: {
      handleOpen(key, keyPath) {
        // console.log(key, keyPath);
      },
      handleClose(key, keyPath) {
        // console.log(key, keyPath);
      }
    },
    mounted(){
        
        this.msg.$on("silde",()=>{
            this.isCollapse = !this.isCollapse;
        })
    },
    computed: {
        ...mapState({
            meau: state => state.user.meau,
            isLogin: state => state.user.userInfo
        })
    },
    created() {
       
    }
};
</script>

<style lang='scss' scoped>
.aside{
    float: left;
    height: calc(100% - 61px);
}
#sidebar{
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden
}
</style>



