import Vue from 'vue'
import Router from 'vue-router'
const _import = file => () =>
import ('@/view/' + file + '.vue')

Vue.use(Router)
export default new Router({
  mode: process.env.NODE_ENV == "production" ? "history" : 'hash',
  // scrollBehavior(to, from, savedPosition) { // router-link滚动条行为
  //   return { x: 0, y: 0 }
  // },
  routes: [{
    path: '/',
    name: 'home',
    component: _import('home/index'),
    meta: {
      title: '首页'
    }
  },
    {
      path: '/home/index',
      name: 'home',
      component: _import('home/index'),
      meta: {
        title: '首页'
      }
    },
    {
      path: '/login',
      name: 'login',
      component: _import('login/login'),
      meta: {
        title: '登陆'
      }
    },
    {
      path: '/register',
      name: 'register',
      component: _import('register/register'),
      meta: {
        title: '注册'
      }
    },
    {
      path: '/forgotPass',
      name: 'forgotPass',
      component: _import('forgotPass/forgotPass'),
      meta: {
        title: '找回密码'
      }
    },
    {
      path: '/statistics/statistics_site',
      name: 'statistics_site',
      component: _import('statistics/statistics_site'),
      meta: {
        title: '站点统计'
      }
    },
    {
      path: '/statistics/statistics_amount',
      name: 'statistics_amount',
      component: _import('statistics/statistics_amount'),
      meta: {
        title: '资金统计'
      }
    },
    {
      path: '/statistics/statistics_order',
      name: 'statistics_order',
      component: _import('statistics/statistics_order'),
      meta: {
        title: '订单统计'
      }
    },
    {
      path: '/statistics/statistics_lang',
      name: 'statistics_lang',
      component: _import('statistics/statistics_lang'),
      meta: {
        title: '中文站点'
      }
    },
    {
      path: '/usermanage/user_list',
      name: 'user_list',
      component: _import('usermanage/user_list'),
      meta: {
        title: '用户管理列表'
      }
    },
    {
      path:'/activity',
      name:'activity',
      component:_import('activity/index'),
      meta:{
        title:'活动页面'
      }
    },
    {
      path: '/usermanage/user_detail',
      name: 'user_detail',
      component: _import('usermanage/user_detail'),
      meta: {
        title: '用户详情'
      }
    },
    {
      path: '/productmanage/product_list',
      name: 'product_list',
      component: _import('productmanage/product_list'),
      meta: {
        title: '商品管理'
      }
    },
    {
      path: '/productmanage/product_insert',
      name: 'product_insert',
      component: _import('productmanage/product_insert'),
      meta: {
        title: '新建商品'
      }
    },
    {
      path: '/productmanage/product_detail/:id',
      name: 'product_detail',
      component: _import('productmanage/product_detail'),
      meta: {
        title: '商品详情'
      }
    },
    {
      path: '/coupon/coupon_list',
      name: 'coupon_list',
      component: _import('coupon/coupon_list'),
      meta: {
        title: '优惠码列表'
      }
    },
    {
      path: '/coupon/send_batch',
      name: 'send_batch',
      component: _import('coupon/send_batch'),
      meta: {
        title: '批量发券'
      }
    },
    {
      path: '/coupon/send_review',
      name: 'send_review',
      component: _import('coupon/send_review'),
      meta: {
        title: '批量发券详情'
      }
    },
    {
      path: '/withdrawHistory',
      name: 'withdrawHistory',
      component: _import('withdrawHistory/index'),
      meta: {
        title: '提现列表'
      }
    },
    {
      path: '/banner',
      name: 'banner',
      component: _import('tool/banner'),
      meta: {
        title: 'banner上传'
      }
    },
    {
      path: '/tool/coupon_send',
      name: 'coupon_send',
      component: _import('tool/coupon_send'),
      meta: {
        title: '优惠券发放'
      }
    },
    {
      path:'/ordermanage/hosting',
      name: 'hosting',
      component: _import('ordermanage/hosting'),
      meta: {
        title: '托管订单'
      }
    },
    {
      path:'/ordermanage/after_order_add',
      name: 'after_order_add',
      component: _import('ordermanage/after_order_add'),
      meta: {
        title: '新增售后订单'
      }
    },
    {
      path:'/ordermanage/after_order_list',
      name: 'after_order_list',
      component: _import('ordermanage/after_order_list'),
      meta: {
        title: '售后订单列表'
      }
    },
    {
      path: '/tool/export',
      name: 'export',
      component: _import('tool/data_export'),
      meta: {
        title: '数据导出'
      }
    },
    {
      path: '/account/google_code',
      name: 'google_code',
      component: _import('account/google_code'),
      meta: {
        title: '谷歌验证'
      }
    },
    {
      path: '/account/set_account',
      name: 'set_account',
      component: _import('account/set_account'),
      meta: {
        title: '管理员设置'
      }
    },
    {
      path: '/add_banner',
      name: 'add_banner',
      component: _import('tool/add_banner'),
      meta: {
        title: 'banner上传'
      }
    },
    {
      path: '/fundHistory',
      name: 'fundHistory',
      component: _import('fundHistory/index'),
      meta: {
        title: '充值'
      }
    },
    {
      path: '/coupon/add_coupon',
      name: 'coupon_list',
      component: _import('coupon/add_coupon'),
      meta: {
        title: '新增优惠码'
      }
    },
    {
      path: '/coupon/send_coupon',
      name: 'coupon_list',
      component: _import('coupon/send_coupon'),
      meta: {
        title: '发送优惠码'
      }
    },
    {
      path: '/ordermanage/order_list',
      name: 'order_list',
      component: _import('ordermanage/order_list'),
      meta: {
        title: '订单列表'
      }
    },
    {
      path: '/ordermanage/order_detail',
      name: 'order_detail',
      component: _import('ordermanage/order_detail'),
      meta: {
        title: '订单详情'
      }
    },
    {
      path: '/offlinemanager/offline_order_detail',
      name: 'offline_order_detail',
      component: _import('offlinemanager/offline_order_detail'),
      meta: {
        title: '线下订单详情'
      }
    },
    {
      path: '/ordermanage/order_sending',
      name: 'order_sending',
      component: _import('ordermanage/order_sending'),
      meta: {
        title: '发货管理'
      }
    },
    {
      path: '/ordermanage/order_hosted',
      name: 'order_hosted',
      component: _import('ordermanage/order_hosted'),
      meta: {
        title: '部署托管'
      }
    },
    {
      path: '/ordermanage/batch_deliver',
      name: 'batch_deliver',
      component: _import('ordermanage/batch_deliver'),
      meta: {
        title: '批量发货'
      }
    },
    {
      path: '/ordermanage/batch_deploy',
      name: 'batch_deploy',
      component: _import('ordermanage/batch_deploy'),
      meta: {
        title: '批量部署'
      }
    },
    {
      path: '/ordermanage/review_batch',
      name: 'review_batch',
      component: _import('ordermanage/review_batch'),
      meta: {
        title: '批量发货(确认)'
      }
    },
    {
      path: '/ordermanage/review_deploy',
      name: 'review_deploy',
      component: _import('ordermanage/review_deploy'),
      meta: {
        title: '批量托管(确认)'
      }
    },
    {
      path: '/offlinemanager/offline_input',
      name: 'offline_input',
      component: _import('offlinemanager/offline_input'),
      meta: {
        title: '录入线下订单'
      }
    },
    {
      path: '/offlinemanager/offline_order_list',
      name: 'offline_order_list',
      component: _import('offlinemanager/offline_order_list'),
      meta: {
        title: '线下订单列表'
      }
    },
    {
      path: '/offlinemanager/offline_user',
      name: 'offline_user',
      component: _import('offlinemanager/offline_user'),
      meta: {
        title: '线下用户列表'
      }
    },
    {
      path: '/offlinemanager/offline_user_info',
      name: 'offline_user_info',
      component: _import('offlinemanager/offline_user_info'),
      meta: {
        title: '线下用户信息'
      }
    }
  ]
})


